import { createServerSupabaseClientWithCookies } from '@/lib/supabase/server';
import { NextRequest, NextResponse } from 'next/server';
import { headers, cookies } from 'next/headers';

/**
 * Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù API ÏóîÎìúÌè¨Ïù∏Ìä∏
 * AuthRedirectHandlerÏóêÏÑú ÏÇ¨Ïö©ÏûêÏùò Ïù∏Ï¶ù ÏÉÅÌÉúÎ•º Í≤ÄÏ¶ùÌïòÎäî Îç∞ ÏÇ¨Ïö©Îê©ÎãàÎã§.
 */
export async function GET(request: NextRequest) {
  try {
    console.log('üîç [Auth Verify API] Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù ÏöîÏ≤≠ Î∞õÏùå');

    // App RouterÏóêÏÑú Ïø†ÌÇ§Î•º ÏùΩÏùÑ Ïàò ÏûàÎäî ÏÑúÎ≤ÑÏÇ¨Ïù¥Îìú Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ±
    const cookieStore = await cookies();
    
    const supabase = createServerSupabaseClientWithCookies({
      get: (name: string) => {
        const cookie = cookieStore.get(name);
        return cookie ? { name: cookie.name, value: cookie.value } : undefined;
      },
      set: (cookie: { name: string; value: string; [key: string]: any }) => {
        cookieStore.set(cookie.name, cookie.value, cookie);
      }
    });

    console.log('üîç [Auth Verify API] Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ± ÏôÑÎ£å');

    // Î®ºÏ†Ä Îπ†Î•∏ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÌôïÏù∏ (getUserÎäî getSessionÎ≥¥Îã§ Îπ†Î•¥Í≥† ÏïàÏ†ïÏ†Å)
    const { data: userData, error: userError } = await supabase.auth.getUser();
    
    console.log('üîç [Auth Verify API] ÏÇ¨Ïö©Ïûê Ï°∞Ìöå Í≤∞Í≥º:', {
      hasUser: !!userData?.user,
      userId: userData?.user?.id,
      userError: userError?.message,
    });
    
    if (userError || !userData?.user) {
      console.warn('‚ö†Ô∏è [Auth Verify API] ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï°∞Ìöå Ïò§Î•ò ÎòêÎäî ÏÇ¨Ïö©Ïûê ÏóÜÏùå:', userError);
      return NextResponse.json(
        { 
          valid: false, 
          error: 'User authentication failed',
          message: 'ÏÇ¨Ïö©Ïûê Ïù∏Ï¶ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.'
        }, 
        { status: 401 }
      );
    }

    // Ï£ºÏùò: getUser()Í∞Ä ÏÑ±Í≥µÌñàÎã§Î©¥ ÌÜ†ÌÅ∞Ïù¥ Ïú†Ìö®Ìï®ÏùÑ ÏùòÎØ∏
    // Î≥ÑÎèÑÏùò ÏÑ∏ÏÖò ÎßåÎ£å Ï≤¥ÌÅ¨Îäî getUser() Ìò∏Ï∂ú ÏûêÏ≤¥ÏóêÏÑú Ï≤òÎ¶¨Îê®


    // ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï°¥Ïû¨ ÌôïÏù∏ (ÏÑ†ÌÉùÏ†Å)
    try {
      const { data: profile, error: profileError } = await supabase
        .from('user_profiles')
        .select('id, email, deleted_at')
        .eq('id', userData.user.id)
        .single();

      if (profileError && profileError.code !== 'PGRST116') { // PGRST116 = row not found
        console.warn('‚ö†Ô∏è [Auth Verify API] ÌîÑÎ°úÌïÑ Ï°∞Ìöå Ïò§Î•ò:', profileError);
      }

      // ÏÇ≠Ï†úÎêú ÏÇ¨Ïö©Ïûê Ï≤¥ÌÅ¨
      if (profile?.deleted_at) {
        console.warn('üóëÔ∏è [Auth Verify API] ÏÇ≠Ï†úÎêú ÏÇ¨Ïö©Ïûê Í≥ÑÏ†ï');
        return NextResponse.json(
          { 
            valid: false, 
            error: 'Account deleted',
            message: 'ÏÇ≠Ï†úÎêú Í≥ÑÏ†ïÏûÖÎãàÎã§.'
          }, 
          { status: 401 }
        );
      }
    } catch (profileError) {
      console.warn('‚ö†Ô∏è [Auth Verify API] ÌîÑÎ°úÌïÑ Ï≤¥ÌÅ¨ Ï§ë Ïò§Î•ò (ÏßÑÌñâ Í≥ÑÏÜç):', profileError);
      // ÌîÑÎ°úÌïÑ Ï≤¥ÌÅ¨ Ïã§Ìå®Îäî Ïù∏Ï¶ù Ïã§Ìå®Î°ú Ï≤òÎ¶¨ÌïòÏßÄ ÏïäÏùå
    }

    // WeChat Î°úÍ∑∏Ïù∏ ÌäπÎ≥Ñ Í≤ÄÏ¶ù
    const provider = userData.user.app_metadata?.provider;
    if (provider === 'wechat') {
      console.log('üîÑ [Auth Verify API] WeChat Î°úÍ∑∏Ïù∏ ÌäπÎ≥Ñ Í≤ÄÏ¶ù');
      
      // WeChat ÌÜ†ÌÅ∞ Ïú†Ìö®ÏÑ± Ï∂îÍ∞Ä Í≤ÄÏ¶ù (ÌïÑÏöîÏãú Íµ¨ÌòÑ)
      // ÌòÑÏû¨Îäî Í∏∞Î≥∏ ÏÇ¨Ïö©Ïûê Í≤ÄÏ¶ùÏúºÎ°ú Ï∂©Î∂Ñ
    }

    console.log('‚úÖ [Auth Verify API] Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù ÏÑ±Í≥µ:', {
      userId: userData.user.id,
      email: userData.user.email,
      provider: provider || 'email',
    });

    return NextResponse.json({
      valid: true,
      user: {
        id: userData.user.id,
        email: userData.user.email,
        provider: provider || 'email',
      },
    });
  } catch (error) {
    console.error('üí• [Auth Verify API] Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò:', error);
    
    return NextResponse.json(
      { 
        valid: false, 
        error: 'Internal server error',
        message: 'ÏÑúÎ≤Ñ ÎÇ¥Î∂Ä Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.'
      }, 
      { status: 500 }
    );
  }
}

/**
 * OPTIONS ÏöîÏ≤≠ Ï≤òÎ¶¨ (CORS ÏßÄÏõê)
 */
export async function OPTIONS(request: NextRequest) {
  return new NextResponse(null, {
    status: 200,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type, Authorization',
    },
  });
}